<template>
    <div class="WoXin">
        <tou/>
       <div id='cont'>
          <div id="info" style="color: #fff;" v-if="txt.length>2">
            <img :src="imgA" />
            <div class="infoR">
              <p class="p1">{{txt}}</p>
              <p style="margin: 0;padding: 0;">暂无绑定手机号码</p>
          </div>
            <p>
              <router-link to='/wodezi'>
              <van-icon name="arrow" style="font-size: .8rem;color: #fff;"/>

              </router-link>
            </p>
          </div>
       <div id="info" style="color: #fff;" v-if="txt==''">
            <img :src="faA" alt=""/>
            <div class="infoR">
              <p class="p1">
                <router-link to='/denglu'>
                  登录/注册
                </router-link>
                
                </p>
              <p style="margin: 0;padding: 0;">请先登录</p>
          </div>
            <p>
              <van-icon name="arrow" style="font-size: .8rem;color: #fff;"/>
            </p>
          </div>
        <ul class='my'>
          <li>
            <p><span class="nums1" style="font-size: .6rem">0.00</span> 元</p>
            <span>我的余额</span>
          </li>
          <li>
            <p><span class="nums2" style="font-size: .6rem">3</span> 个</p>
            <span>我的优惠</span>
          </li>
          <li>
            <p><span class="nums3" style="font-size: .6rem">0</span> 分</p>
            <span>我的积分</span>
          </li>
        </ul>

        <ul class='list1'>
          <li>
            <span>我的订单</span>
            <span>
              <van-icon name="arrow" style="font-size: .6rem;color: #000;"/>
            </span>
          </li>
          <li>
            <span>积分商城</span>
            <span>
              <van-icon name="arrow" style="font-size: .6rem;color: #000;"/>
            </span>
          </li>
          <li>
            <span>饿了么会员卡</span>
            <span>
              <van-icon name="arrow" style="font-size: .6rem;color: #000;"/>
            </span>
          </li>
        </ul>
         <ul class='list2'>
          <li>
            <span>服务中心</span>
            <span>
              <van-icon name="arrow" style="font-size: .6rem;color: #000;"/>
            </span>
          </li>
          <li>
            <span>下载饿了么APP</span>
            <span>
              <van-icon name="arrow" style="font-size: .6rem;color: #000;"/>
            </span>
          </li>
        </ul>
        </div>
        <wei/>
    </div>
</template>
<script>
import tou from '../zhuye/toubiao'
import wei from '../zhuye/dibu'
export default {
    components:{
        tou,
        wei
    },
    data(){
      return{
           txt:'',
           imgA:'https://img.yzcdn.cn/vant/logo.png'
      }
    },
    methods:{
       faA(){
         if(localStorage.vuex){
           this.txt=JSON.parse(localStorage.vuex).name
         }
         
       }
    },
    
    mounted(){
       this.faA()
       if(localStorage.img){
           this.imgA=localStorage.img
         }
    }
}
</script>
<style  scoped>
  #cont{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 1.5rem;
    bottom: 0;
    left: 0;
    right: 0;
    background: #f5f5f5;
  }
  #cont>a{
    color: #fff;
  }
  #info{
    width: 100%;
    height: 4rem;
    background: #3190e8;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
  #info>img{
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
  }
  #info>.infoR{
    width: 70%;
    height: 1.8rem;
    /* border: 1px solid ; */
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    color:#fff;
    font-size: .6rem;
  }
  #info>.infoR>.p1{
    font-size: .6rem;
    font-weight: bold;
    margin: 0;
    padding: 0;
  }
  #info>p{
    margin: 0;
    padding: 0;
    width: .3rem;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: .32rem;
    font-weight: bold;
  }
  #info>p a{
    color: #fff;
  }
  .my{
    width: 100%;
    height: 1.7rem;
    display: flex;
    justify-content: space-between;
  }
  .my>li{
    width: 33%;
    height: 1.7rem;
    background: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: .24rem;
  }
  .my>li>span{
    font-size: .4rem;
    font-weight: bold;
    color:#595856;
  }
  .my>li>p{
    margin: 0;
    padding: 0;
  }
  .my>li>p span{
    font-size: .46rem;
    font-weight: bold;
  }
  .nums1{
    color: #ff8f00;
  }
  .nums2{
    color:#ff5934;
  }
  .nums3{
    color:#6ac102;
  }
  .list1,.list2{
    width: 100%;
    margin-top: .2rem;
    background: #fff;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  .list1>li,.list2>li{
    width: 95%;
    height: 1.2rem;
    /* margin: 0 auto; */
    border-bottom: #f4f4f4 .01rem solid;
    box-sizing: border-box;
    font-size: .5rem;
    margin-bottom: .02rem;
    display: flex;
    align-items: center;
    /* background: url('../../../images/head-user.png') no-repeat left center; */
    background-size: 6%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-indent: 1.2rem;
  }

</style>


